.Team {
    margin-top: 300px;
    position: relative;
}

.gallery {
    text-align: center;
    position: relative;
}

.firstTeam {
    background-color: #fff;
    box-shadow: 10px 10px 10px 10px #ccc;
    display: inline-block;
    position: relative;
    top: -100px;
    padding: 10px;
    width: 150px;
}

.firstTeam:hover {
    background-color: #00b0ff;
    color: #fff;
    z-index: 4;
}

