.name {
    color: #66d0ff
}

.h1Bottom {
    font-size: 15vw;
    text-align: center;
    padding: 0;
    margin: 0;
    color: #f7f7f7;
}

.leftArrow {
    position: absolute;
    left: 20%;
    top: 56%;
    color: #0022ff;
    border-radius: 100%;
    padding: 10px;
    box-shadow: 3px 4px 3px 5px #ccc;
    z-index: 2;
}

.rightArrow {
    position: absolute;
    right: 20%;
    bottom: 40%;
    color: #0022ff;
    border-radius: 100%;
    padding: 10px;
    box-shadow: 3px 4px 3px 5px #ccc;
    z-index: 2;
}

.rightArrow:hover {
    background-color:  #0022ff;
    color: #fff;
    cursor: pointer;
}

.leftArrow:hover {
    background-color:  #0022ff;
    color: #fff;
    cursor: pointer;
}

@media (max-width: 1300px) {
    .rightArrow {
        left: 50%;
        top: 10%;
    }

    .leftArrow {
        left: 50%;
        top: 100%;
    }
}

@media (max-width: 1100px) {
    .h1Bottom {
        position: relative;
        top: -30px;
    }

    .rightArrow {
        top: 0;
    }
}

@media (max-width: 700px) {
    .gallery {
        top: 50px;
    }
}

